export default {
  name: "IndexHello",

  data() {
    return {
      currentIndex: 0,
      //
      timer: null,
      //
      imgArr: [{
        id: 0,
        url: 'http://g7.glypro19.com:8088/photo/Souye1.webp'
      }, {
        id: 1,
        url: " http://g7.glypro19.com:8088/photo/Souye2.webp"
      }, {
        id: 2,
        url: "http://g7.glypro19.com:8088/photo/Souye3.webp"
      }, {
        id: 3,
        url: "http://g7.glypro19.com:8088/photo/Souye4.webp"
      }]
    };
  },

  methods: {
    //
    startInterval() {
      // 
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        this.currentIndex++;

        if (this.currentIndex > this.imgArr.length - 1) {
          this.currentIndex = 0;
        }
      }, 3000);
    },

    // 
    clickIcon(val) {
      if (val === 'down') {
        this.currentIndex++;

        if (this.currentIndex === this.imgArr.length) {
          this.currentIndex = 0;
        }
      } else {
        /* 
        this.currentIndex--;
        if(this.currentIndex < 0){
          this.currentIndex = this.imgArr.length-1;
        } */
        // 
        if (this.currentIndex === 0) {
          this.currentIndex = this.imgArr.length;
        }

        this.currentIndex--;
      }
    },

    // 
    changeImg(index) {
      this.currentIndex = index;
    },

    //
    changeInterval(val) {
      if (val) {
        clearInterval(this.timer);
      } else {
        this.startInterval();
      }
    }

  },

  //
  mounted() {
    this.startInterval();
  }

};