const {
  ElMessage
} = require("element-plus");

export default {
  name: "LoginPage",

  data() {
    return {
      loginForm: {
        username: "liwei",
        password: "lw1234"
      },
      passwordType: "password",
      rules: {
        username: [{
          required: true,
          message: 'Please input Activity name',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: 'Please input Activity name',
          trigger: 'blur'
        }]
      }
    };
  },

  methods: {
    login() {
      // 1usernamepassword
      // 2axiosSpringBoot
      console.log("", this.loginForm);
      this.$api.login.login("/login", this.loginForm).then(res => {
        console.log("", res); // vuex
        //token

        console.log(res.token);
        sessionStorage.setItem("token", res.token); // 
        // token

        this.$api.menu.findNavTree("menu/findNavTree", {
          'userName': this.loginForm.username
        }).then(res => {
          console.log("", res); //this.$store.commit('setNavTree',res);
          // this.$store.dispatch('setNavTree',{res});//
          // 

          this.$store.dispatch({
            // dispatch
            type: 'setNavTree',
            //typeactions
            data: res
          });
          return new Promise((resolve, reject) => {
            let a = false;
            resolve(this.loginForm.username);

            if (a) {
              reject("");
            }
          });
        }).then(res => {
          console.log("", res);
          this.$api.user.findPermissions("user/findPermissions", {
            'name': res
          }).then(res => {
            console.log(this.loginForm.username + "" + res);
            this.$store.commit('setPerms', res);
          });
        });
        this.$router.push("/index");
      }).catch(err => {
        console.log(err);
        this.$router.push("/"); //
      });
    },

    changeState() {
      if (this.passwordType === 'password') {
        this.passwordType = 'text';
      } else {
        this.passwordType = 'password';
      }
    }

  }
};