export default {
  name: "OrderManager",

  data() {
    return {
      tableData0: [{
        order_id: "ID",
        id: "ID",
        name: "",
        nums: "",
        num: "",
        out_trade_no: "",
        order_solve: "",
        generation_time: ""
      }]
    };
  },

  methods: {
    dell() {//
    },

    fin(out_trade_no, id) {
      //==>yes12ok,=>
      //
      this.$api.order.FinishOrder('/OrderManager/FinishOrder', {
        out_trade_no,
        id,
        "order_solve": "finish"
      }).then(res => {
        console.log({
          "": res
        });
        this.$message.success('');
        this.$router.push({
          path: '/index/mainContent/AllOrder'
        }); //
      }).catch(res => {
        console.log({
          "": res
        });
        this.$message.err('');
      });
    },

    audit(out_trade_no, id) {
      //
      this.$api.order.FinishOrder('/OrderManager/auditOrder', {
        out_trade_no,
        id,
        "order_solve": "audit"
      }).then(res => {
        console.log({
          "": res
        });
        this.$message.success('');
        this.$router.push({
          path: '/index/mainContent/AllOrder'
        }); //
      }).catch(res => {
        console.log({
          "": res
        });
        this.$message.err('');
      });
    },

    find() {
      //
      this.$api.order.findOrder('/OrderManager/AllOrder', "", {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(res => {
        console.log({
          "": res
        });
        this.$message.success('');

        for (let i = 0; i < res.length; i++) {
          this.tableData0.push(res[i]);
        }
      }).catch(res => {
        console.log({
          "": res
        });
        this.$message.err('');
      });
    }

  }
};